const convertDateToDDMMYYYY = (value: Date | string) => {
  if (!value) return '';
  const date = new Date(value);
  const day = date.getDate() < 10 ? `0${date.getDate()}` : date.getDate();
  const month =
    date.getMonth() + 1 < 10 ? `0${date.getMonth() + 1}` : date.getMonth() + 1;
  const year = date.getFullYear();
  return `${day}/${month}/${year}`;
};

const convertDateToDDMMYYYY_H_M = (value: Date | string) => {
  if (!value) return '';
  const date = new Date(value);
  const day = date.getDate() < 10 ? `0${date.getDate()}` : date.getDate();
  const month =
    date.getMonth() + 1 < 10 ? `0${date.getMonth() + 1}` : date.getMonth() + 1;
  const year = date.getFullYear();
  const hours = date.getHours() < 10 ? `0${date.getHours()}` : date.getHours();
  const minutes =
    date.getMinutes() < 10 ? `0${date.getMinutes()}` : date.getMinutes();
  return `${day}/${month}/${year} ${hours}:${minutes}`;
};

const convertToDateWithZeroTime = (dateInput: Date) => {
  const date = new Date(dateInput);
  date.setUTCHours(0, 0, 0, 0);
  return date.toISOString();
};

const checkIsOrderThen18 = (dateInput?: Date | string) => {
  if (!dateInput) {
    return false;
  }
  const today = new Date();
  const birthDate = new Date(dateInput);
  const age = today.getFullYear() - birthDate.getFullYear();
  const m = today.getMonth() - birthDate.getMonth();
  if (m < 0 || (m === 0 && today.getDate() < birthDate.getDate())) {
    return age > 18;
  }
  return age >= 18;
};

const convertTime = (totalMilliseconds: number) => {
  const totalSeconds = Math.floor(totalMilliseconds / 1000);

  const days = Math.floor(totalSeconds / 86400);
  const remainingSecondsAfterDays = totalSeconds % 86400;

  const hours = Math.floor(remainingSecondsAfterDays / 3600);
  const remainingSecondsAfterHours = remainingSecondsAfterDays % 3600;

  const minutes = Math.floor(remainingSecondsAfterHours / 60);

  return `${days}d${hours}h${minutes}m`;
};

const getCurrentTime = (data?: Date | string) => {
  if (!data) return '';
  const now = new Date(data);
  const h = String(now.getHours()).padStart(2, '0');
  const m = String(now.getMinutes()).padStart(2, '0');
  const s = String(now.getSeconds()).padStart(2, '0');
  return `${h}:${m}:${s}`;
};

const formatDateVietnamese = (data: Date | string) => {
  if (!data) return '';
  const date = new Date(data);
  const weekdays = [
    'Chủ nhật',
    'Thứ 2',
    'Thứ 3',
    'Thứ 4',
    'Thứ 5',
    'Thứ 6',
    'Thứ 7',
  ];

  const dayOfWeek = weekdays[date.getDay()];
  const day = String(date.getDate()).padStart(2, '0');
  const month = String(date.getMonth() + 1).padStart(2, '0');
  const year = date.getFullYear();

  return `${dayOfWeek}, ${day}/${month}/${year}`;
};

export default {
  convertDateToDDMMYYYY,
  convertToDateWithZeroTime,
  checkIsOrderThen18,
  convertDateToDDMMYYYY_H_M,
  convertTime,
  getCurrentTime,
  formatDateVietnamese,
};
