import {MenuViewItem} from '../model/interface/service.interface';

const hasPermission = (
  listMenuView: MenuViewItem[],
  nameId?: string | null | string[],
) => {
  if (!nameId) return true;
  if (typeof nameId === 'string')
    return listMenuView?.some(view => view.nameId === nameId);
  const listPermission = listMenuView?.map(m => m.nameId);
  const isCheckShow = nameId
    ?.map(i => listPermission?.includes(i))
    .find(i => i);
  return !!isCheckShow;
};

export default {hasPermission};
