import {Linking, Platform, Share} from 'react-native';
import DocumentPicker from 'react-native-document-picker';
import Toast from 'react-native-toast-message';
import RNFS, {DownloadFileOptions} from 'react-native-fs';
import ReactNativeBlobUtil from 'react-native-blob-util';
import {isProduct, setConfig} from '../services/httpClient';
import {dataEnv, TypeOfDataEnv} from '../constants/Env';
import CryptoJS from 'crypto-js';

const getSize = (size?: number) => {
  if (!size) return '-';
  if (size && size >= 1024) {
    return Math.floor(size / 1024) + ' KB';
  } else if (size && size < 1024) {
    return size + 'B';
  } else {
    return '-';
  }
};
const handleGetFile = async () => {
  try {
    const data = await DocumentPicker.pick({
      type: [DocumentPicker.types.allFiles],
    });
    if (data?.[0].size && data?.[0].size >= 10485760) {
      Toast.show({
        type: 'error',
        text1: 'Thông báo',
        text2: 'Dung lượng tệp tin quá lớn (tối đa 10MB)',
      });
      return null;
    }
    const fileUri =
      Platform.OS === 'ios'
        ? data?.[0].uri.replace('file://', '')
        : data?.[0].uri;
    return {
      uri: fileUri,
      type: data?.[0].type,
      name: data?.[0]?.name,
    };
  } catch (error) {
    if (DocumentPicker.isCancel(error)) {
      console.log('User cancelled the picker');
    } else {
      console.error(error);
    }
    return null;
  }
};

const downloadFile = async (
  fileName: string,
  baseUrl: string,
  isShare = true,
) => {
  try {
    let statusDownload = '';
    const toFile = `${
      Platform.OS === 'android'
        ? RNFS.DocumentDirectoryPath
        : RNFS.DocumentDirectoryPath
    }/${fileName}`;
    const dataBase64 = await ReactNativeBlobUtil.fetch('GET', baseUrl)
      .then(res => {
        let status = res.info().status;
        if (status == 200) {
          let base64Str = res.base64();
          return base64Str;
        } else return null;
      })
      .catch(() => null);
    if (dataBase64) {
      await RNFS.writeFile(toFile, dataBase64, 'base64');
      if (isShare) {
        const dataShare = await Share.share({
          url: toFile,
        });
        if (dataShare.action == Share.dismissedAction) {
          statusDownload = 'error';
          await RNFS.unlink(toFile);
        } else {
          statusDownload = 'success';
        }
      }

      return {
        uri: Platform.OS === 'ios' ? toFile : 'file://' + toFile,
        status: statusDownload,
      };
    } else {
      return {
        uri: '',
        status: 'error',
      };
    }
  } catch (error) {
    return {
      uri: '',
      status: 'error',
    };
  }
};

const generateBoxShadowStyle = (
  xOffset: number,
  yOffset: number,
  shadowColorIos: string,
  shadowOpacity: number,
  shadowRadius: number,
  elevation: number,
  shadowColorAndroid: string,
) => {
  if (Platform.OS === 'ios') {
    return {
      shadowColor: shadowColorIos,
      shadowOffset: {width: xOffset, height: yOffset},
      shadowOpacity,
      shadowRadius,
    };
  } else if (Platform.OS === 'android') {
    return {
      elevation,
      shadowColor: shadowColorAndroid,
    };
  }
};

const getDefaultConfig = (system: TypeOfDataEnv) => {
  setConfig((dataEnv as any)[system][isProduct]);
};

const parseQueryParamsString = (queryString: string) => {
  const params: any = {};
  const keyValuePairs = queryString.split('&');
  for (const pair of keyValuePairs) {
    const [key, value] = pair.split('=');
    params[key] = value;
  }
  return params;
};

const mimeTypes = {
  jpg: 'image/jpeg',
  jpeg: 'image/jpeg',
  png: 'image/png',
  gif: 'image/gif',
  webp: 'image/webp',
  svg: 'image/svg+xml',
  mp4: 'video/mp4',
  mp3: 'audio/mpeg',
  wav: 'audio/wav',
  pdf: 'application/pdf',
  doc: 'application/msword',
  docx: 'application/vnd.openxmlformats-officedocument.wordprocessingml.document',
  xls: 'application/vnd.ms-excel',
  xlsx: 'application/vnd.openxmlformats-officedocument.spreadsheetml.sheet',
  ppt: 'application/vnd.ms-powerpoint',
  pptx: 'application/vnd.openxmlformats-officedocument.presentationml.presentation',
  txt: 'text/plain',
  csv: 'text/csv',
  json: 'application/json',
  zip: 'application/zip',
  rar: 'application/vnd.rar',
  '7z': 'application/x-7z-compressed',
};

const getMimeType = (fileName: any) => {
  if (!fileName) return 'application/octet-stream';
  const ext = fileName.split('.').pop().toLowerCase();
  return (mimeTypes as any)[ext] || 'application/octet-stream'; // Giá trị mặc định nếu không tìm thấy
};

// HMAC-SHA256 userId->token
// SECERT_KEY=userId+userId
// PLAIN_TEXT=deviceId+timestamp+ipGateway+latitude+longitude
const hmacSha256CryptoJS = (message: string, key: string) => {
  const hash = CryptoJS.HmacSHA256(message, key);
  return hash.toString(CryptoJS.enc.Base64);
};
const getSignature = (data: any, userId?: number) => {
  if (!userId) return '';
  const SECERT_KEY = '' + userId + userId;
  const PLAIN_TEXT =
    (data.deviceId || '') +
    (data.ssid || '') +
    (data.timestamp || '') +
    (data.ipGateway || '') +
    (data.latitude || '') +
    (data.longitude || '');
  console.log('PLAIN_TEXT', PLAIN_TEXT);
  return hmacSha256CryptoJS(PLAIN_TEXT, SECERT_KEY.toString());
};
const openPermissionSettings = (perm?: 'camera' | 'location') => {
  if (Platform.OS === 'ios') {
    switch (perm) {
      case 'camera':
        try {
          return Linking.openURL('App-Prefs:root=Privacy&path=CAMERA');
        } catch (error) {
          console.log(error);
          return Linking.openSettings();
        }
      case 'location':
        try {
          return Linking.openURL(
            'App-Prefs:root=Privacy&path=LOCATION_SERVICES',
          );
        } catch (error) {
          console.log(error);
          return Linking.openSettings();
        }
      default:
        return Linking.openURL('app-settings:');
    }
  } else {
    // Android
    if (!perm) {
      return Linking.openSettings();
    }
    if (perm === 'location') {
      return Linking.sendIntent('android.settings.LOCATION_SOURCE_SETTINGS');
    }
    if (perm === 'camera') {
      // Không có intent riêng cho camera; mở chung App Settings
      return Linking.openSettings();
    }
  }
};

export default {
  getSize,
  handleGetFile,
  downloadFile,
  generateBoxShadowStyle,
  getDefaultConfig,
  parseQueryParamsString,
  getMimeType,
  getSignature,
  openPermissionSettings,
};
